import React, { Component } from 'react'
import { Link } from 'react-router-dom';

import { caricaClassifica } from '../../API/ApiClassifica';

export default class ClassificaSocieta extends Component {

    constructor(props) {
        super();

        this.nome_soc = props.logo.substr(props.logo.lastIndexOf("/"));

        let arr = [];
        props.squadre.forEach(sq => {
            arr.push({
                camp_id: sq.camp,
                idt: sq.id,
                camp_nome: sq.camp_abbr,
                nome: ((sq.nome !== "") ? sq.nome : sq.small),
                loaded: false
            });
        });
        console.log(arr);

        this.state = {
            squadre: arr,
            all_loaded: false,
            classifiche: []
        }
    }

    componentDidMount() {
        this.state.squadre.forEach((sq, i) => {
            //console.log("loading " + i)
            caricaClassifica(sq.camp_id, (clas) => {
                let obj = clas.filter(e => e.logo.substr(e.logo.lastIndexOf("/")) == this.logo)[0];
                if (obj != undefined && obj.gte != 0) {
                    let sq_ind = this.state.squadre.indexOf(this.state.squadre.filter(s => s.camp_id == obj.camp)[0]);
                    obj.categoria = this.state.squadre[sq_ind].camp_nome;
                    obj.idt = this.state.squadre[sq_ind].idt;
                    this.state.classifiche.push(obj);
                    //this.state.squadre[sq_ind]
                    console.log(obj);
                    this.forceUpdate();
                }
            });
        });
    }

    allLoaded(arr) {
        arr.forEach(e => {
            if (!e.loaded)
                return false;
        });
        return true;
    }

    render() {
        return (
            <div>
                {this.state.classifiche.map(c => <ClassificaElem {...c} />)}
            </div>
        )
    }
}

function ClassificaElem(props) {
    return (
        <div className="card card-body highlight cls-card">
            <Link to={"/squadra/" + props.idt} className="link-unstyled">
                <table className="w-100">
                    <tbody>
                        <tr>
                            <td className="cls-col1">{props.categoria}</td>
                            <td className="cls-col2">{props.nome}</td>
                            <td className="cls-col3">
                                <b>{props.pos}<sup>o</sup></b>
                            </td>
                            <td className="cls-col4">
                                <b>{props.punti}</b> pti
                            </td>
                        </tr>
                    </tbody>
                </table>
            </Link>
        </div>
    )
}
